
.profile{
          width: 300px; 
           height:400px;
           box-shadow: 5px 10px #ac7b7b;
           border-image-slice: 20%;
            order: 1;
             animation: profile__animate 9s ease-in-out infinite 1s;
            }
@keyframes profile__animate {
    0% {
        border-radius: 60% 40% 30% 70%/ 60% 30% 70% 40%;
    }
    50% {
        border-radius: 30% 60% 70% 40%/ 50% 60% 30% 60%;
    }
    100% {
        border-radius: 60% 40% 30% 70%/ 60% 30% 70% 40%;
    }

}
.profile1{
    width: 250px; 
     height:300px;
     box-shadow: 5px 10px 4px #8f8f96;
     border-image-slice: 50%;
      order: 1;
       animation: profile__animate 9s ease-in-out infinite 1s;
      }
@keyframes profile__animate {
0% {
  border-radius: 50% 40% 30% 90%/ 60% 30% 70% 50%;
}
50% {
  border-radius: 90% 60% 70% 40%/ 40% 60% 70% 40%;
}
100% {
  border-radius: 50% 40% 30% 90%/ 60% 30% 70% 50%;
}

}